          {'label' :'密码','name':  'pwd' ,'id':'pwd' ,'placehoder':'请输入8-20位的密码'},
          {'label' :'确认密码','name':  'cpwd' ,'id':'cpwd' ,'placehoder':'两次输入的密码不一致'},
          {'label' :'手机号','name':  'phone' ,'id':'phone' ,'placehoder':'请输入正确的手机号码'},
          {'label' :'图形验证码','name':  'pic_code' ,'id':'pic_code' ,'placehoder':'请填写图形验证码','class':'msg_input'},
